.button {
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: none;
  border-radius: 0.25rem;
  transition: background-color 0.3s cubic-bezier(0.5, 0, 0.1, 1), color 0.3s cubic-bezier(0.5, 0, 0.1, 1), box-shadow 0.3s cubic-bezier(0.5, 0, 0.1, 1);
}

.button-primary {
  background-color: #007bff;
  color: #fff;
}

.button-secondary {
  background-color: #6c757d;
  color: #fff;
}

.button-outline {
  background-color: transparent;
  border: 2px solid currentColor;
  color: #007bff;
}

.button-large {
  padding: 0.75rem 1.5rem;
  font-size: 1.2rem;
}

.button-small {
  padding: 0.25rem 0.5rem;
  font-size: 0.8rem;
}

.button-disabled {
  opacity: 0.7;
  cursor: not-allowed;
  pointer-events: none;
}

.button:hover,
.button:focus {
  outline: none;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}

.button:active {
  transform: translateY(1px);
}

.carousel {
  position: relative;
  overflow: hidden;
}

.carousel-item {
  display: none;
  position: relative;
}

.carousel-item.active {
  display: block;
}

.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px; /* Adjust as needed */
  height: 50px; /* Adjust as needed */
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  line-height: 50px; /* Center content vertically */
  cursor: pointer;
  transition: background-color 0.3s cubic-bezier(0.5, 0, 0.1, 1);
}

.carousel-control:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.carousel-control.prev {
  left: 0;
}

.carousel-control.next {
  right: 0;
}

.carousel-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.carousel-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: background-color 0.3s cubic-bezier(0.5, 0, 0.1, 1);
}

.carousel-indicator.active {
  background-color: #fff;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1050;
}

.modal-open {
  display: flex;
}

.modal-dialog {
  background-color: #fff;
  max-width: 600px; /* Adjust as needed */
  padding: 20px;
  border-radius: 0.25rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  opacity: 0;
  transform: translateY(-50px);
  transition: opacity 0.3s cubic-bezier(0.5, 0, 0.1, 1), transform 0.3s cubic-bezier(0.5, 0, 0.1, 1);
}

.modal-open .modal-dialog {
  opacity: 1;
  transform: translateY(0);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.modal-title {
  font-size: 1.2rem;
  font-weight: 600;
}

.modal-body {
  padding-bottom: 10px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid #ddd;
  padding-top: 10px;
  margin-top: 10px;
}

.modal-footer button {
  margin-left: 10px;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 1.2rem;
}

@media (max-width: 576px) {
  .modal-dialog {
    width: 90%;
    max-width: none;
  }
}
